<script lang="ts">
	import type { PageProps } from './$types';
	import { getTranslation, type Language } from '$lib/i18n';
	import { cart } from '$lib/stores/cart.svelte';
	import { enhance } from '$app/forms';
	import { page } from '$app/stores';

	let { data }: PageProps = $props();

	let language = $derived<Language>(data.language);

	// Check if payment was cancelled
	const paymentCancelled = $derived($page.url.searchParams.get('payment_cancelled'));

	function t(key: any) {
		return getTranslation(language, key);
	}

	function formatPrice(price: number): string {
		return `€${price.toFixed(2)}`;
	}

	let switchingStudent = $state(false);
	let studentCode = $state('');

	// Track single selected photo ID (null = none selected)
	let selectedPhotoId = $state<number | null>(null);

	// Reset selection when data changes (new photos loaded)
	$effect(() => {
		// Watch for data.photos changes
		const photoIds = data.photos.map((p) => p.id);

		// If currently selected photo is not in the new photo list, clear selection
		if (selectedPhotoId !== null && !photoIds.includes(selectedPhotoId)) {
			selectedPhotoId = null;
		}
	});

	// Filter products based on selected photo
	const filteredProducts = $derived(() => {
		if (selectedPhotoId === null) {
			// No photo selected - show no products (selection is mandatory)
			return [];
		}

		// Filter products that:
		// 1. Have relatedPhotoIds = null (universal products available for all photos), OR
		// 2. Have at least one relatedPhotoId that matches the selected photo
		const filtered = (data.products || []).filter((product) => {
			// Universal products (no photo link or linked to non-preview photos)
			if (product.relatedPhotoIds === null) {
				return true;
			}

			// Check if any related photo ID matches the selected photo
			// selectedPhotoId is checked for null above, so safe to use here
			return selectedPhotoId !== null && product.relatedPhotoIds.includes(selectedPhotoId);
		});

		return filtered;
	});

	function selectPhoto(photoId: number) {
		// Select this photo (no toggle, selection is mandatory)
		selectedPhotoId = photoId;
	}
</script>

<div class="min-h-screen bg-gray-50">
	<div class="max-w-7xl mx-auto p-3 sm:p-6">
		<!-- Welcome Section -->
		<div class="bg-white rounded-lg shadow-md p-4 sm:p-6 mb-4 sm:mb-6">
			<h1 class="text-xl sm:text-3xl font-bold text-gray-800">
				{t('welcome')}, {data.student.firstName}
				{data.student.lastName}!
			</h1>
			<p class="text-sm sm:text-base text-gray-600 mt-2">{t('personalCatalogue')}</p>
		</div>

		<!-- Payment Cancelled Message -->
		{#if paymentCancelled}
			<div class="bg-yellow-50 border border-yellow-200 rounded-lg p-4 mb-4 sm:mb-6">
				<div class="flex items-start gap-3">
					<svg
						class="w-6 h-6 text-yellow-600 flex-shrink-0"
						fill="none"
						stroke="currentColor"
						viewBox="0 0 24 24"
					>
						<path
							stroke-linecap="round"
							stroke-linejoin="round"
							stroke-width="2"
							d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
						></path>
					</svg>
					<div>
						<p class="font-medium text-yellow-800">Payment Cancelled</p>
						<p class="text-sm text-yellow-700 mt-1">
							Your payment for order {paymentCancelled} was cancelled. You can add items to your cart
							and try again.
						</p>
					</div>
				</div>
			</div>
		{/if}

		<!-- Sibling Switcher -->
		{#if data.siblings && data.siblings.length > 0}
			<div class="bg-white rounded-lg shadow-md p-3 sm:p-4 mb-4 sm:mb-6">
				<h2 class="text-base sm:text-lg font-semibold text-gray-800 mb-3">
					{t('switchToSibling')}
				</h2>
				<div class="flex flex-wrap gap-2">
					{#each data.siblings as sibling}
						<form
							method="POST"
							action="?/switchStudent"
							use:enhance={() => {
								switchingStudent = true;
								return async ({ update }) => {
									await update();
									switchingStudent = false;
									window.location.reload();
								};
							}}
						>
							<input type="hidden" name="studentId" value={sibling.id} />
							<button
								type="submit"
								disabled={switchingStudent}
								class="px-4 py-2 rounded-lg font-medium transition-colors bg-purple-100 text-purple-700 hover:bg-purple-200 disabled:opacity-50 disabled:cursor-not-allowed"
							>
								{sibling.firstName}
								{sibling.lastName}
							</button>
						</form>
					{/each}
				</div>
			</div>
		{/if}

		<!-- Year Selector -->
		{#if data.availableYears && data.availableYears.length > 1}
			<div class="bg-white rounded-lg shadow-md p-3 sm:p-4 mb-4 sm:mb-6">
				<h2 class="text-base sm:text-lg font-semibold text-gray-800 mb-3">{t('selectYear')}</h2>
				<div class="flex flex-wrap gap-2">
					{#each data.availableYears as year}
						<a
							href="?year={year}"
							class="px-4 py-2 rounded-lg font-medium transition-colors {data.selectedYear === year
								? 'bg-blue-600 text-white'
								: 'bg-gray-100 text-gray-700 hover:bg-gray-200'}"
						>
							{year}
						</a>
					{/each}
				</div>
			</div>
		{/if}

		<!-- Switch to Another Student -->
		<div class="bg-white rounded-lg shadow-md p-3 sm:p-4 mb-4 sm:mb-6">
			<h2 class="text-base sm:text-lg font-semibold text-gray-800 mb-3">
				{t('addAnotherStudent')}
			</h2>
			<form
				method="POST"
				action="?/addStudentByCode"
				use:enhance={() => {
					switchingStudent = true;
					return async ({ update }) => {
						await update();
						switchingStudent = false;
					};
				}}
			>
				<div class="flex flex-col sm:flex-row gap-2">
					<input
						type="text"
						name="code"
						bind:value={studentCode}
						placeholder={t('enterStudentCode')}
						class="flex-1 px-3 sm:px-4 py-2 border border-gray-300 rounded-lg focus:border-blue-500 focus:outline-none text-sm sm:text-base"
					/>
					<button
						type="submit"
						disabled={switchingStudent || studentCode.replace(/\D/g, '').length !== 10}
						class="w-full sm:w-auto px-4 sm:px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed text-sm sm:text-base"
					>
						{switchingStudent ? t('loading') : t('addStudent')}
					</button>
				</div>
			</form>
		</div>

		<!-- Photo Gallery -->
		<div class="bg-white rounded-lg shadow-md p-3 sm:p-6 mb-4 sm:mb-6">
			<h2 class="text-lg sm:text-xl font-semibold text-gray-800 mb-4">{t('yourPhotos')}</h2>

			{#if data.photos.length === 0}
				<div class="text-center py-12 text-gray-500">
					<svg
						class="mx-auto h-12 w-12 text-gray-400 mb-4"
						fill="none"
						stroke="currentColor"
						viewBox="0 0 24 24"
					>
						<path
							stroke-linecap="round"
							stroke-linejoin="round"
							stroke-width="2"
							d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"
						/>
					</svg>
					<p>{t('noPhotos')}</p>
				</div>
			{:else}
				<div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
					{#each data.photos as photo (photo.id)}
						<button
							type="button"
							class="border rounded-lg overflow-hidden hover:shadow-lg transition-all relative group cursor-pointer"
							class:ring-4={selectedPhotoId === photo.id}
							class:ring-blue-500={selectedPhotoId === photo.id}
							onclick={() => selectPhoto(photo.id)}
						>
							<div class="absolute top-2 right-2 z-10">
								<div
									class="w-6 h-6 rounded-full border-2 flex items-center justify-center transition-colors"
									class:bg-blue-600={selectedPhotoId === photo.id}
									class:border-blue-600={selectedPhotoId === photo.id}
									class:bg-white={selectedPhotoId !== photo.id}
									class:border-gray-300={selectedPhotoId !== photo.id}
									class:group-hover:border-blue-400={selectedPhotoId !== photo.id}
								>
									{#if selectedPhotoId === photo.id}
										<svg
											width="16"
											height="16"
											viewBox="0 0 24 24"
											fill="none"
											stroke="white"
											stroke-width="3"
										>
											<path d="M20 6L9 17l-5-5" />
										</svg>
									{/if}
								</div>
							</div>
							<div class="aspect-square bg-gray-200 flex items-center justify-center">
								<img src={photo.path} alt={photo.name} class="w-full h-full object-cover" />
							</div>
							<div class="p-2 bg-white">
								<p class="text-sm text-gray-700 text-center truncate">{photo.name}</p>
							</div>
						</button>
					{/each}
				</div>
			{/if}
		</div>

		<!-- Products -->
		<div class="bg-white rounded-lg shadow-md p-3 sm:p-6">
			<h2 class="text-lg sm:text-xl font-semibold text-gray-800 mb-4">{t('products')}</h2>

			{#if !data.products || data.products.length === 0}
				<div class="text-center py-12 text-gray-500">
					<svg
						class="mx-auto h-12 w-12 text-gray-400 mb-4"
						fill="none"
						stroke="currentColor"
						viewBox="0 0 24 24"
					>
						<path
							stroke-linecap="round"
							stroke-linejoin="round"
							stroke-width="2"
							d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
						/>
					</svg>
					<p>{t('noProducts')}</p>
				</div>
			{:else if selectedPhotoId === null}
				<div class="text-center py-12 text-blue-600">
					<svg class="mx-auto h-12 w-12 mb-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
						<path
							stroke-linecap="round"
							stroke-linejoin="round"
							stroke-width="2"
							d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"
						/>
					</svg>
					<p class="text-lg font-medium">{t('selectPhotoToViewProducts')}</p>
				</div>
			{:else if filteredProducts().length === 0}
				<div class="text-center py-12 text-gray-500">
					<svg
						class="mx-auto h-12 w-12 text-gray-400 mb-4"
						fill="none"
						stroke="currentColor"
						viewBox="0 0 24 24"
					>
						<path
							stroke-linecap="round"
							stroke-linejoin="round"
							stroke-width="2"
							d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
						/>
					</svg>
					<p>{t('noProductsForSelectedPhotos')}</p>
				</div>
			{:else}
				<div class="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
					{#each filteredProducts() as product (product.photoLinkId)}
						<div
							class="border rounded-lg overflow-hidden hover:shadow-lg transition-shadow relative flex flex-col h-full"
						>
							{#if product.isRecommended}
								<div
									class="absolute top-2 right-2 z-10 bg-yellow-500 text-white px-3 py-1 rounded-full text-sm font-semibold shadow-lg"
								>
									⭐ {t('recommended')}
								</div>
							{/if}
							<!-- Product Icons -->
							<div class="relative w-full pb-[100%] bg-gray-200 flex-shrink-0">
								<div class="absolute inset-0 flex items-center justify-center p-8">
									{#if product.iconUrls && product.iconUrls.length > 0}
										<div
											class="grid gap-1 w-full h-full"
											class:grid-cols-1={product.iconUrls.length === 1}
											class:grid-cols-2={product.iconUrls.length > 1}
										>
											{#each product.iconUrls as iconUrl}
												<img
													src={iconUrl}
													alt={product.productName}
													class="w-full h-full object-contain"
												/>
											{/each}
										</div>
									{:else}
										<svg
											class="w-20 h-20 text-gray-400"
											fill="none"
											stroke="currentColor"
											viewBox="0 0 24 24"
										>
											<path
												stroke-linecap="round"
												stroke-linejoin="round"
												stroke-width="2"
												d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"
											/>
										</svg>
									{/if}
								</div>
							</div>
							<!-- Product Info -->
							<div class="p-4 flex flex-col flex-1 text-center">
								<h3
									class="font-normal text-base mb-2 line-clamp-2 min-h-[3rem] flex items-center justify-center"
								>
									{product.productName}
								</h3>

								<!-- Price section with fixed height structure -->
								<div class="flex flex-col items-center gap-1 mb-3 min-h-[3rem]">
									{#if product.discount > 0}
										<div class="text-gray-500 line-through text-sm">
											{formatPrice(product.originalPrice)}
										</div>
										<div class="flex items-center gap-2">
											<span class="text-green-600 font-bold text-lg">
												{formatPrice(product.price)}
											</span>
											<span class="text-xs bg-green-100 text-green-800 px-2 py-1 rounded">
												-{product.discount.toFixed(0)}%
											</span>
										</div>
									{:else}
										<div class="text-gray-900 font-bold text-lg">
											{formatPrice(product.price)}
										</div>
									{/if}
								</div>

								<div class="mt-auto">
									{#if cart.getQuantity(product.photoLinkId) > 0}
										<!-- Quantity Controls -->
										<div class="flex items-center gap-2">
											<button
												onclick={() =>
													cart.updateQuantity(
														product.photoLinkId,
														cart.getQuantity(product.photoLinkId) - 1
													)}
												class="flex-1 bg-red-500 text-white py-2 rounded-lg hover:bg-red-600 transition-colors flex items-center justify-center"
												aria-label="Decrease quantity"
											>
												<svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
													<path
														stroke-linecap="round"
														stroke-linejoin="round"
														stroke-width="2"
														d="M20 12H4"
													/>
												</svg>
											</button>
											<div
												class="px-4 py-2 bg-gray-100 rounded-lg font-semibold text-center min-w-[60px]"
											>
												{cart.getQuantity(product.photoLinkId)}
											</div>
											<button
												onclick={() =>
													cart.updateQuantity(
														product.photoLinkId,
														cart.getQuantity(product.photoLinkId) + 1
													)}
												class="flex-1 bg-green-600 text-white py-2 rounded-lg hover:bg-green-700 transition-colors flex items-center justify-center"
												aria-label="Increase quantity"
											>
												<svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
													<path
														stroke-linecap="round"
														stroke-linejoin="round"
														stroke-width="2"
														d="M12 4v16m8-8H4"
													/>
												</svg>
											</button>
										</div>
									{:else}
										<!-- Add to Cart Button -->
										<button
											onclick={() => cart.add(product)}
											class="w-full bg-blue-600 text-white py-2 rounded-lg hover:bg-blue-700 transition-colors flex items-center justify-center gap-2"
										>
											<svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
												<path
													stroke-linecap="round"
													stroke-linejoin="round"
													stroke-width="2"
													d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z"
												/>
											</svg>
											{t('addToCart')}
										</button>
									{/if}
								</div>
							</div>
						</div>
					{/each}
				</div>
			{/if}
		</div>
	</div>
</div>
